export default {
	props: {
		// 当前状态码
		state: {
			type: Number,
			default: 1
		},
		//区别不同来源
		type: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {

		}
	},
	methods: {

	},
	computed: {
		stateName() {
			switch (this.state) {
				case -1:
					return "申请退单";
				case -2:
					return "已关闭";
				case 0:
					return "已创建";
				case 1:
					return "已报价";
				case 2:
					return "待确认合同";
				case 3:
					return "已签单";
				case 4:
					return "待开工";
				case 5:
					return "待监理确认";
				case 6:
					return "施工中";
				case 7:
					return "待验收";
				case 8:
					return "已竣工";
				case 10:
					return "已保存";
				case 11:
					return "待派单";
				case 12:
					return "增减项目";
				case 13:
					return "增减项目";
				case 14:
					return "增减项目";
				case 15:
					return "待客户确认";
				case 16:
					return "待确认合同";
				default:
					return "已创建";
			}
		},
		stateClassName() {
			switch (this.state) {
				case 8:
					return "finish";
				case 2:
					return "going";
				default:
					return "going";
			}
		},
		stateStatus(){
			switch(this.state){
				case 8: return "已完工";
				default: return "施工中";
			}
		}
	}
}
